@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
*{border:0;outline:0;padding:0;vertical-align:baseline;box-sizing:border-box;font-weight:inherit;font-family:inherit;font-style:inherit;font-size:100%}body{background:#00c6ff;background:linear-gradient(to right, #0072ff, #00c6ff)}.main-container{width:50%;height:50%;background:rgba(255,255,255,0.25);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);backdrop-filter:blur(4px);-webkit-backdrop-filter:blur(4px);border-radius:10px;border:1px solid rgba(255,255,255,0.18);top:50%;left:50%;position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.main-container .quote-container{font-family:roboto;font-weight:500;color:white;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:0}

